$('.portfolio__slider').slick({
    fade: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    appendDots: $('.portfolio__dots'),
    customPaging : function(slider, i) {
        var thumb = $(slider.$slides[i]).data();

        return '0' + (i + 1);
    },
    dotsClass: 'portfolio__dots-list',
    prevArrow: $('.portfolio__prev'),
    nextArrow: $('.portfolio__next'),
});

