$(document).ready(function() {

// BurgerMenu
    $('.menu-btn').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    $('.menu2-btn').click(function () {
        $(this).toggleClass('burger--active')
        $('.sidebar__menu').toggleClass('sidebar__menu--active')
        $('body').toggleClass('fixed-body')
    })


//Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list option').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list option").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list option')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Before - After
    const slider = document.querySelector('.slider');
    const before = document.querySelector('.before');
    const beforeImage = before.querySelector('img');
    const change = document.querySelector('.change');
    const body = document.body;

    let isActive = false;

    document.addEventListener('DOMContentLoaded', () => {
        let width = slider.offsetWidth;
        beforeImage.style.width = `${width}px`;
    });

    change.addEventListener('mousedown', () => {
        isActive = true;
    });

    body.addEventListener('mouseup', () => {
        isActive = false;
    });

    body.addEventListener('mouseleave', () => {
        isActive = false;
    });

    const beforeAfterSlider = (x) => {
        let shift = Math.max(0, Math.min(x, slider.offsetWidth));
        before.style.width = `${shift}px`;
        change.style.left = `${shift}px`;
    };

    const pauseEvents = (e) => {
        e.stopPropagation();
        e.preventDefault();
        return false;
    };

    body.addEventListener('mousemove', (e) => {
        if (!isActive) {
            return;
        }

        let x = e.pageX;
        x -= slider.getBoundingClientRect().left;
        beforeAfterSlider(x);
        pauseEvents(e);
    });

    change.addEventListener('touchstart', () => {
        isActive = true;
    });

    body.addEventListener('touchend', () => {
        isActive = false;
    });

    body.addEventListener('touchcancel', () => {
        isActive = false;
    });

    body.addEventListener('touchmove', (e) => {
        if (!isActive) {
            return;
        }

        let x;

        let i;
        for (i = 0; i < e.changedTouches.length; i++) {
            x = e.changedTouches[i].pageX;
        }

        x -= slider.getBoundingClientRect().left;

        beforeAfterSlider(x);
        pauseEvents(e);
    });


    // RandomImages
    function Displayrndimages() {

        var images = [
            'img/info-img2.webp',
            'img/info-img3.webp',
            'img/info-img4.webp',
        ]

        var Rndimages = images[Math.floor(Math.random() * images.length)]
        var image = "<img src='"+ Rndimages +"'>"

        document.getElementById('showrndimages').innerHTML = image
    }

    setInterval(Displayrndimages,'1500')

    // Ranges
    const rangeSlider = document.getElementById('range-slider');

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 500
            },
            pips: {
                mode: 'values',
                values: [20, 100, 180, 260, 340, 420, 500],
            }
        });

        const rangeNumber = document.querySelector('.range-number');
        const rangeNumber1 = document.querySelector('.range-number-1');

        rangeSlider.noUiSlider.on('update', function (values, handle) {
            rangeNumber.innerHTML = Math.floor(values[handle]);
            rangeNumber1.innerHTML = Math.floor(values[handle]);
        });

    }

    //
    const rangeSlider2 = document.getElementById('range-slider2');

    if (rangeSlider2) {
        noUiSlider.create(rangeSlider2, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 60
            },
            pips: {
                mode: 'values',
                values: [0, 10, 20, 30, 40, 50, 60],
            }
        });

        const rangeNumber2 = document.querySelector('.range-number2');
        const rangeNumber21 = document.querySelector('.range-number2-1');

        rangeSlider2.noUiSlider.on('update', function (values, handle) {
            rangeNumber2.innerHTML = Math.floor(values[handle]);
            rangeNumber21.innerHTML = Math.floor(values[handle]);
        });


    }

    //
    const rangeSlider3 = document.getElementById('range-slider3');

    if (rangeSlider3) {
        noUiSlider.create(rangeSlider3, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 500
            },
            pips: {
                mode: 'values',
                values: [20, 100, 180, 260, 340, 420, 500],
            }
        });

        const rangeNumber3 = document.querySelector('.range-number3');
        const rangeNumber31 = document.querySelector('.range-number3-1');

        rangeSlider3.noUiSlider.on('update', function (values, handle) {
            rangeNumber3.innerHTML = Math.floor(values[handle]);
            rangeNumber31.innerHTML = Math.floor(values[handle]);
        });

    }

    //
    const rangeSlider4 = document.getElementById('range-slider4');

    if (rangeSlider4) {
        noUiSlider.create(rangeSlider4, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 30
            },
            pips: {
                mode: 'values',
                values: [0, 6, 12, 18, 24, 30],
            }
        });

        const rangeNumber4 = document.querySelector('.range-number4');
        const rangeNumber41 = document.querySelector('.range-number4-1');

        rangeSlider4.noUiSlider.on('update', function (values, handle) {
            rangeNumber4.innerHTML = Math.floor(values[handle]);
            rangeNumber41.innerHTML = Math.floor(values[handle]);
        });

    }

    //
    const rangeSlider5 = document.getElementById('range-slider5');

    if (rangeSlider5) {
        noUiSlider.create(rangeSlider5, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 50
            },
            pips: {
                mode: 'values',
                values: [0, 10, 20, 30, 40, 50],
            }
        });

        const rangeNumber5 = document.querySelector('.range-number5');
        const rangeNumber51 = document.querySelector('.range-number5-1');

        rangeSlider5.noUiSlider.on('update', function (values, handle) {
            rangeNumber5.innerHTML = Math.floor(values[handle]);
            rangeNumber51.innerHTML = Math.floor(values[handle]);
        });

    }

    //
    const rangeSlider6 = document.getElementById('range-slider6');

    if (rangeSlider6) {
        noUiSlider.create(rangeSlider6, {
            start: 0,
            connect: 'lower',
            step: 1,
            range: {
                'min': 0,
                'max': 2500
            },
            pips: {
                mode: 'values',
                values: [0, 500, 1000, 1500, 2000, 2500],
            }
        });

        const rangeNumber6 = document.querySelector('.range-number5');
        const rangeNumber61 = document.querySelector('.range-number5-1');

        rangeSlider6.noUiSlider.on('update', function (values, handle) {
            rangeNumber6.innerHTML = Math.floor(values[handle]);
            rangeNumber61.innerHTML = Math.floor(values[handle]);
        });

    }

})

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}